const app = getApp();

export default {
    data() {
        return {
            count: 60,
            mobile_number: '',
            token: '',
            btn_txt: '获取验证码',
            show_weixin: true ,
            code : ''
        };
    },
    onLoad() {

        uni.setStorageSync('wexin_connect', false);
    },
    onShow() {
        let wexin_connect = uni.getStorageSync('wexin_connect');
        if (wexin_connect.openid) {
         //   this.show_weixin = false;
        }
    },


    methods: {
        wx_login: function () {
            uni.login({
                provider: 'weixin',
                success: (loginRes) => {
                    console.log(JSON.stringify(loginRes));
                    wx.setStorageSync('wexin_connect', loginRes.authResult);
                    let $params = loginRes.authResult;
                    let action = this.bra_service('bra_app', 'connect_weixin_user');
                    let promise = this.fetch(action, $params);
                    promise.then((data) => {
                        console.log(data)
                        this.log_user_in(data.user, data => {
                            return this.onNav({
                                url: '/pages/hotel/index/index', type: 'switch'
                            });
                        })
                    }, err => {
                        console.log(err)
                        this.show_toast('您还没有绑定注册账号 请先注册账号!');
                        //this.show_weixin = false;
                    })
                }
            });
        },

        recount: function () {
            var that = this;
            if (that.count > 0) {
                setTimeout(function () {
                    that.count--;
                    that.recount()
                }, 1000);
                this.btn_txt = that.count;
            } else {
                that.count = 60;
                this.btn_txt = "获取验证码";
            }
        },
        send_sms: function (mobile) {
            let action = this.bra_service('bra', 'send_login_verify_code');
            var that = this;
            let params = {mobile: that.mobile_number};
            params.is_mixed = 2;
            let promise = this.fetch(action, params , 'post');
            promise.then((data) => {
                uni.showToast({
                    icon: "none",
                    title: '已经发送',
                    duration: 2000
                });
                this.recount();
            }, error => {
                setTimeout(function () {
                    uni.showToast({
                        icon: "none",
                        title: error.msg,
                        duration: 2000
                    });
                } , 100);

            })
        },
        send_verify_code() {
            this.send_sms(this.mobile_number);
        } ,

        verify_code() {
            if(this.code.length < 4){
                return uni.showToast({
                    icon : "none",
                    title:  '请输入正确的验证码',
                    duration: 2000
                });
            }
            let action = this.bra_service('bra', 'verify_login_code');
            var that = this;
            let promise = this.fetch(action, {mobile: that.mobile_number , code : this.code});
            promise.then(res => {
                let data = res.data;
                if(data.user){
                    this.log_user_in(data.user , () =>{
                        console.log(data);

                        this.$store.commit('change_login', true);
                        this.onNav({
                            url : '/pages/hotel/index/index', type : 'switch'
                        });
                    });
                }else{
                    uni.showToast({
                        icon : "none",
                        title:  data.msg || '系统错误',
                        duration: 2000
                    });
                }

                console.log(data);
            } , error => {
                uni.showToast({
                    icon : "none",
                    title:  error.msg || '系统错误',
                    duration: 2000
                });
            })
        }
    }
}
